<template>
    <div>
      <div class="w3-center">
        <h1>Account Management</h1>
      </div>
      <div class="w3-container w3-margin-bottom">
          <div class="w3-row align-h-center align-v-center field">
            <label class="w3-third w3-right-align bold">Username:</label>
             <label class="w3-third w3-left-align margin-left-10">{{appGlobal.user.username}}</label>
          </div>

          <div class="w3-row align-h-center align-v-center field">
             <label class="w3-third w3-right-align bold">Password:</label>
             <input type="password" class="w3-third w3-left-align margin-left-10" id="password"
                    v-model="appGlobal.user.password" disabled/>
          </div>
      </div>

      <div class="w3-container w3-section">
          <button class="w3-button w3-blue margin-right-10" @click="showModal">Update</button>
          <button class="w3-button w3-blue margin-right-10" @click="togglePassword">Show / Hide Password</button>
          <button class="w3-button w3-blue" @click="doRoute('')">Close</button>  
      </div>
    </div>

    <div v-show="state.modalShow == true" class="w3-modal" style="display: block;">
        <div class="w3-modal-content">
            <div class="w3-center">
                <h1>Change Password</h1>
            </div>

            <button @click="showModal" style="position: fixed" class="w3-btn w3-circle w3-display-topright w3-card-4">X</button>

            <div style="min-width: 250px; min-height: 300px;">
                <div class="w3-row align-h-center align-v-center w3-containter w3-section">
                     <label class="w3-cell w3-third w3-right-align align-h-center bold">Current Password</label>

                     <input type="password" class="w3-cell w3-third w3-left-align w3-round-large w3-card-4" v-model="state.ChangePassword.current">
                </div>

                <div class="w3-row align-h-center align-v-center w3-containter w3-section">
                     <label class="w3-cell w3-third w3-right-align align-h-center bold">New Password</label>

                     <input type="password" class="w3-cell w3-third w3-left-align w3-round-large w3-card-4" v-model="state.ChangePassword.new">
                </div>

                <div class="w3-row align-h-center align-v-center w3-containter w3-section">
                     <label class="w3-cell w3-third w3-right-align align-h-center bold">Confirm Password</label>

                     <input type="password" class="w3-cell w3-third w3-left-align w3-round-large w3-card-4" v-model="state.ChangePassword.confirm">
                </div>

                 <div class="w3-container w3-section">
                    <button class="w3-button w3-blue margin-right-10" @click="submit">Submit</button>

                    <button class="w3-button w3-blue" @click="showModal">Cancel</button>  
                </div>

                <div class="w3-container w3-section">
                    <label class="align-h-center bold">{{state.errorMsg}}</label>
                </div>
              </div>
        </div>
    </div>
</template>

<script>
  import { reactive, ref } from 'vue';
  import { useRouter } from 'vue-router';
  import {appGlobal} from '@/store/global/appGlobal.js';
  import { userRepository } from '@/store/repository/userRepository.js';
  export default {
      name: 'UserProfile',

      setup() {
        const state = reactive({
            show: false,
            modalShow: false,
            ChangePassword: {
                current: '',
                new: '',
                confirm: ''
            },
            errorShow: false,
            errorMsg: ''
        });

         const {
               updatePpassword,
               getUserById
            } = userRepository();
        function show() {
            state.show = !state.show;
        }

        const router = useRouter();

        function doRoute(whereTo) {
            router.push('/' + whereTo);
        }

        function clearChangePassword() {
            state.ChangePassword.current = ''
            state.ChangePassword.new = ''
            state.ChangePassword.confirm = ''
        }

        async function validate() {
            if(state.ChangePassword.current != appGlobal.user.password) {
                state.errorMsg = 'Incorrect Password'
            } else if (state.ChangePassword.new != state.ChangePassword.confirm) {
                state.errorMsg = 'Confirm Password does not match New Password'
            } else if (state.ChangePassword.new =='' || state.ChangePassword.confirm=='') {
                state.errorMsg = 'New and Confirm Password cannot be blank'
            } else {
                state.errorMsg =''
            }
        }

        async function submit() {
            const confVal = ref(0);
            validate();
            if( state.errorMsg == '') {
                confVal.value = await updatePpassword(appGlobal.user, state.ChangePassword.new);
            }
            if( confVal.value < 1) {
                state.errorMsg = 'Error Changing password, could not find or authenticate user'
            } else {
                appGlobal.user = await getUserById(appGlobal.user.userId)
                console.log(appGlobal.user)
                showModal();
            }
        }

        function showModal() {
            state.modalShow = !state.modalShow;
            clearChangePassword()
        }

        function togglePassword() {
          let input = document.getElementById('password');

          if (input.type === 'password') {
            input.type = 'text';

          } else {
            input.type = 'password';
          }
        }
        return {
            state,
            appGlobal,
            show,
            doRoute,
            showModal,
            submit,
            togglePassword,
            clearChangePassword
        }
      }
  }
</script>

<style scoped>
#password {
    border-width: 0;
    background-color: transparent;
}
.bold {
    font-weight: bold;
}
.spacing {
    margin-top:25px;
}
</style>