<template>
  <div v-show="showModal === true" class="w3-modal" style="display: block">
    <div class="w3-modal-content" style="width: 90%">
      <button @click="closeMe" style="position: fixed"
              class="w3-btn w3-circle w3-display-topright w3-card-4">X</button>
      <div>
        <div class="w3-center">
          <h1>Publisher Add Edit</h1>
        </div>
      
        <div class="w3-container">
          <div class="w3-row align-h-center align-v-center">
            <label class="w3-third w3-right-align margin-right-10">Publisher Name</label>

            <input type="text" class="w3-third w3-left-align w3-round-large w3-card-4" v-model="state.publisher.publisherName" @keyup.enter="submit">
          </div>

          <div class="w3-row align-h-center align-v-center margin-top-10">
            <div class="w3-container w3-red" v-show="state.errorMessage !== ''">
              {{ state.errorMessage }}
            </div>
          </div>

          <div class="w3-row align-h-center align-v-center margin-top-10">
            <button class="w3-button w3-green w3-round-large margin-right-15" @click="submit">Sumbit</button>
            <button class="w3-button w3-red w3-round-large margin-left-15" @click="closeMe">Cancel</button>
          </div>
        </div>
        </div>
    </div>
  </div>
</template>

<script>
  import { reactive, watch } from 'vue';
  import { publisherRepository } from '@/store/repository/publisherRepository.js'

  export default {
    name: 'PublisherAddEdit',

    emits: ['close-publisher-add-edit'],

    props: ['showModal', 'publisherId'],

    setup(props, context) {
      const state = reactive({
        publisher: {
          publisherName: ''
        },
        editMode: false,
        errorMessage: ''
      });

      const {
        getPublisherById,
        postPublisher,
        putPublisher
      } = publisherRepository();

      watch(props, async function() {
        if (props.showModal) {
          state.errorMessage = '';

          if (props.publisherId !== 0) {
            state.publisher = await getPublisherById(props.publisherId);

            state.editMode = true;

          } else {
            state.publisher.publisherName = '';

            state.editMode = false;
          }
        }
      });

      async function validate() {
        state.errorMessage = '';

        if (state.publisher.publisherName === '') {
          state.errorMessage = 'Please enter a publisher name';
        }
      }

      async function submit() {
        await validate();

        if (state.errorMessage === '') {
          let returnId;

          if (state.editMode){
            returnId = await putPublisher(state.publisher);

          } else {
            returnId = await postPublisher(state.publisher);
          }
          if (returnId === 0) { //Publisher already exists
            state.errorMessage = 'This publisher already exists in the system';

          } else if (returnId === -1) { //Error in API
            state.errorMessage = 'An error occured trying to add the publisher to the system';

          } else { //Success
          console.log('here')
            context.emit('close-publisher-add-edit', true);
          }
        }
      }

      function closeMe() {
        context.emit('close-publisher-add-edit', false);
      }

      return {
        state,
        postPublisher,
        validate,
        submit,
        closeMe
      }
    }
  }
</script>

<style scoped>
input {
  width: 10rem;
}
</style>