<template> 
  <div v-show="showModal === true" class="w3-modal" style="display: block">
    <div class="w3-modal-content modal-window" style="width: 90%">

      <button @click="exit" style="position: fixed" class="w3-btn w3-display-topright w3-card-4">X</button>

      <div>
        <div class="w3-container heading">
          <h3>ADD OR EDIT A CHARACTER</h3>
        </div>

        <div class="w3-container character-input">
          <div>
            Character's Name: 
            <input type="text" placeholder="Type Name" v-model="state.character.characterName" @keyup.enter="submit">
          </div>
        </div>

        <div class = "w3-container success" v-show="state.successMessage !== ''">{{ state.successMessage }}</div>
        <div class = "w3-container error" v-show="state.errorMessage !== ''">{{ state.errorMessage }}</div>

        <div class="w3-container">
          <button class="w3-button submit-button" @click="submit">Save</button>
          <button class="w3-button cancel-button" @click="exit">Exit</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import { reactive, watch } from 'vue';
    import { characterRepository } from '@/store/repository/characterRepository.js';

    export default {
        name: 'CharacterAddEdit',

        emits: ['close-character-add-edit'],

        props: ['showModal', 'characterId'],

        setup(props, context) {
            const state = reactive({
                characters: [],
                character: {
                  characterName: ''
                },
                errorMessage: '', 
                successMessage: '', 
                addMode: true, 
                selectedCharacterId: 0
            })

            const {
                getActiveCharacters, 
                getCharacterByName, 
                getCharacterById, 
                postCharacter, 
                putCharacter
            } = characterRepository();

            watch(props, async function() {
              if (props.showModal) {
                state.errorMessage = '';

                if (props.characterId !== 0) {
                  state.character = await getCharacterById(props.characterId);
                  state.addMode = false;

                } else {
                  state.character.characterName = '';

                  state.addMode = true;
                }
              }
            });

            async function validate() {
                state.errorMessage = '';
                state.successMessage = '';

                if (state.character.characterName === '') {
                    state.errorMessage = 'Sorry, there was an error. Try again.';
                }
            }

            async function submit() {
                await validate();
                
                if (state.errorMessage === '') {

                  if (state.addMode) {
                      let newId = await postCharacter(state.character);

                      if (newId === 0) {
                          state.errorMessage = 'This character already exists. Try someone else.';
                      } else if (newId === -1) {
                          state.errorMessage = 'Sorry, there was an error while adding.';
                      } else {
                          state.character.characterName = '';
                          //state.successMessage = 'Success! This character has been added.';
                          context.emit('close-character-add-edit', true);
                      }   
                  }

                  else {
                    let updateId = await putCharacter(state.character);

                    if (updateId === 0) {
                      state.errorMessage = 'It doesn\'t look like you changed anything.';
                    }
                    else if (updateId === -1) {
                      state.errorMessage = 'Sorry, there was an error while updating.';
                    }
                    else {
                      state.character.characterName = '';
                      //state.successMessage = 'Success! The character has been updated.';
                      context.emit('close-character-add-edit', true);
                    }
                  }
                }
            }

            function exit() {
              context.emit('close-character-add-edit', false);
            }

            return {
                state, 
                getActiveCharacters, 
                getCharacterByName,
                submit, 
                validate, 
                exit, 
            }
        }
    }
</script>

<style scoped>

    .heading {
        background-color: var(--blue);
        margin: 8px;
        color: white;
        text-shadow: 2px 2px var(--gray);
    }
    .add-edit-button {
      margin: 8px;
      background-color: var(--gray);
      border-radius: 6px;
      color: white;
    }
    h4 {
      color: var(--blue);
      font-weight: bold;
    }
    .select {
      margin: 16px;
    }
    .character-input {
        margin: 16px;
    }
    .character-update {
      margin: 16px;
    }
    .cancel-button {
        background-color: var(--red);
        margin: 8px;
        border-radius: 6px;
        color: white;
    }
    .submit-button {
        background-color: #28CA4D;
        border-radius: 6px;
        margin: 8px;
        color: white;
    }
    .success {
        margin: 8px;
        padding: 4px;
        background-color: #28CA4D;
    }
    .error {
        margin: 8px;
        padding: 4px;
        background-color: var(--red);
        color: white;
    }
</style>