<template>
  <div>
    <div class="w3-center">
      <h1>Publishers</h1>
    </div>

    <div class="w3-container align-h-center align-v-center margin-top-10">
      <div class="w3-container w3-red" v-show="state.errorMessage !== ''">
        {{ state.errorMessage }}
      </div>
    </div>

    <div>
      <button class="w3-btn w3-green margin-bottom-15 margin-top-15" @click="addNew">Add New</button>
    </div>

    <div class="w3-container">
      <table class="w3-table-all w3-striped w3-bordered w3-card-4">
        <tr class="w3-blue">
          <th class="w3-center"></th>
          <th>Name</th>
          <th class="w3-center"></th>
        </tr>

        <tr v-for="(publisher, id) in state.publishers" :key="id">
          <td class="w3-center cell-center" style="width: 15%">
            <button class="w3-btn w3-round-large w3-yellow" @click="editSelected(publisher.publisherId)">Edit</button>
          </td>

          <td class="w3-left-align cell-v-center" style="width: 10%;">
            {{ publisher.publisherName }}
          </td>

          <td class="w3-center cell-center" style="width: 15%">
            <button class="w3-btn w3-round-large w3-red" @click="deleteSelected(publisher.publisherId)">Delete</button>
          </td>
        </tr>
      </table>
    </div>

    <PublisherAddEdit :showModal="state.showAddEdit" :publisherId="state.selectedPublisherId"
                      @close-publisher-add-edit="closeAddEdit"/>
  </div>
</template>

<script>
  import { reactive, onMounted } from 'vue';
  import { publisherRepository } from '@/store/repository/publisherRepository.js'

  import PublisherAddEdit from '@/views/publisher/PublisherAddEdit.vue';

  export default {
    name: 'PublisherMaintenance',

    components: {
      PublisherAddEdit
    },

    setup() {
      const state = reactive({
        publishers: [],
        showAddEdit: false,
        selectedPublisherId: 0,
        errorMessage: ''
      })

      const {
        getActivePublishers,
        deletePublisher
      } = publisherRepository();

      onMounted(async () => {
        state.publishers = await getActivePublishers();
      });

      function addNew() {
        state.selectedPublisherId = 0;

        state.showAddEdit = true;
      }

      function editSelected(id) {
        state.selectedPublisherId = id;

        state.showAddEdit = true;
      }

      async function deleteSelected(id) {
        let success = await deletePublisher(id);

        if (success === -1) {
          state.errorMessage = 'There was an error deleting the publisher';
        } else {
          state.publishers = await getActivePublishers();
        }

      }

      async function closeAddEdit(result) {
        state.showAddEdit = false;

        if (result === true) {
          state.publishers = await getActivePublishers();
        }
      }

      return {
        state,
        getActivePublishers,
        addNew,
        editSelected,
        deleteSelected,
        closeAddEdit
      }
    }
  }
</script>

<style scoped>
input {
  width: 10rem;
}
</style>