<template>
  <div>
    <div class="w3-center">
      <h1 class="w3-text-teal">Create Account</h1>
    </div>

    <div class="w3-container">
      <div class="w3-row w3-padding">
        <label class="w3-col w3-right-align" style="width: 50%">Username</label>

        <input type="text" class="w3-col w3-round-large w3-card-4" v-model="state.user.username" @keyup.enter="submit" />
      </div>

      <div class="w3-row w3-padding">
        <label class="w3-col w3-right-align" style="width: 50%">Password</label>

        <input type="password" class="w3-col w3-round-large w3-card-4" v-model="state.user.password" @keyup.enter="submit"/>
      </div>

      <div class="w3-row w3-padding">
        <label class="w3-col w3-right-align" style="width: 50%">Confirm Password</label>

        <input type="password" class="w3-col w3-round-large w3-card-4" v-model="state.user.confirmPassword" @keyup.enter="submit"/>
      </div>

      <div class="w3-row w3-padding">
        <div class="w3-container w3-red" v-show="state.errorMessage !== ''">
          {{ state.errorMessage }}
        </div>
      </div>

      <div class="w3-cell-row w3-padding">
        <button class="w3-button w3-green w3-round-large margin-right-15" @click="submit">Create</button>

        <button class="w3-button w3-red w3-round-large margin-left-15" @click="cancel">Cancel</button>
      </div>
    </div>
  </div>
</template>

<script>
  import { reactive } from "vue";
  import { useRouter } from 'vue-router';
  import { userRepository } from '@/store/repository/userRepository.js'
  import { appGlobal } from '@/store/global/appGlobal.js';

  export default {
    name: "CreateAccount",

    setup() {
      const state = reactive({
        user: {
          username: "",
          password: "",
          confirmPassword: "",
        },
        errorMessage: "",
      });

      const router = useRouter();

      const {
        postUser,
        getUserById
      } = userRepository();

      async function validate() {
        state.errorMessage = "";

        if (state.user.username === "" || state.user.password === "" || state.user.confirmPassword === "") {
          state.errorMessage = "Please enter all fields";
        }
        if (state.user.password !== state.user.confirmPassword) {
          state.errorMessage = "Passwords do not match";
        }
      }

      async function submit() {
        await validate();
        if (state.errorMessage === "") {
           let newId = await postUser(state.user);
           
          if (newId === 0) {
            state.errorMessage = 'This username already exists in the system';

          } else if (newId === -1) {
            state.errorMessage = 'An error occured trying to add the username to the system';

          } else {
            appGlobal.user = await getUserById(newId);

            console.log(appGlobal.user);

            router.push('/homescreen');
          }
        }
      }

      function cancel() {
        router.push('/');
      }

      return {
        appGlobal,
        state,
        postUser,
        validate,
        submit,
        cancel
      };
    },
  };
</script>

<style scoped>
input {
  width: 15rem;
}

label {
  align-items: left;
  margin-right: 20px;
}
</style>
