<template>
  <div v-show="showModal === true" class="w3-modal" style="display: block">
    <div class="w3-modal-content" style="width: 700px">
      <div class="w3-button w3-display-topright w3-circle w3-red w3-hover-purple" 
            @click="closeMe">
        &times;
      </div>

      <div class="w3-container w3-cell">
        <p>
          <img src="..\assets\download.jpg" />
        </p>

        <p>
          <button class="w3-button w3-green w3-round-large margin-right-15" v-show="state.editMode === true">Browse</button>
        </p>
      </div>

      <div class="w3-container w3-cell align=left">
        <div class="w3-bar align-h-center align-v-center">
          <div class="w3-bar-item w3-right-align" style="width: 135px">
            <label for="title"><b>Title</b></label>
          </div>

          <div class="w3-bar-item">
            <label v-show="state.editMode === false">{{ state.issue.issueName }}</label>

            <input class="w3-round-large w3-card-4 w3-yellow" style="width: 215px" v-model="state.issue.issueName"
                    id="issueName" v-show="state.editMode === true"/>
          </div>
        </div>

        <div class="w3-bar align-h-center align-v-center">
          <div class="w3-bar-item w3-right-align" style="width: 135px">
            <label for="issue number"><b>Issue Number</b></label>
          </div>

          <div class="w3-bar-item">
            <label v-show="state.editMode === false">{{ state.issue.issueNumber }}</label>

            <input class="w3-round-large w3-card-4 w3-yellow" style="width: 215px" v-model="state.issue.issueNumber"
                    id="issueNumber" v-show="state.editMode === true"/>
          </div>
        </div>

        <div class="w3-bar align-h-center align-v-center">
          <div class="w3-bar-item w3-right-align" style="width: 135px">
            <label for="publisher"><b>Publisher</b></label>
          </div>

          <div class="w3-bar-item">
            <PublisherDropdown :issueId="state.issue.publisherId" @selected-publisher-changed="selectionChanged"/>
          </div>
        </div>

        <div class="w3-bar align-h-center align-v-center">
          <div class="w3-bar-item w3-right-align" style="width: 135px">
            <label for="coverPrice"><b>Cover Price</b></label>
          </div>

          <div class="w3-bar-item">
            <label v-show="state.editMode === false">{{ state.issue.coverPrice }}</label>

            <input class="w3-round-large w3-card-4 w3-yellow" style="width: 215px" v-model="state.issue.coverPrice" 
                    id="coverPrice" v-show="state.editMode === true"/>
          </div>
        </div>

        <div class="w3-bar align-h-center align-v-center">
          <div class="w3-bar-item w3-right-align" style="width: 135px">
            <label for="character tags"><b>Character Tags</b></label>
          </div>

          <div class="w3-bar-item">
            <input class="w3-round-large w3-card-4 w3-yellow" style="width: 215px" v-model="state.issue.characterTags" 
                    id="characterTags"/>
          </div>
        </div>
        
        <p>
          <button class="w3-button w3-green w3-round-large margin-right-15" @click="submit"
                  v-show="state.editMode === true">Save</button>

          <button class="w3-button w3-green w3-round-large margin-right-15" @click="edit"
                  v-show="state.editMode === false">Edit</button>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, watch } from "vue";
import { issueRepository } from "@/store/repository/issueRepository.js";

import PublisherDropdown from "@/components/PublisherDropdown.vue";

export default {
  name: "Issue",
  props: ['showModal', 'issueId'],

  components: {
    PublisherDropdown,
  },

  emits: ['close-issue'],

  setup(props, context) {
    const state = reactive({
      issue: {
        issueName: "",
        issueNumber: "0",
        publisherId: 0,
        coverPrice: 0.00,
        characterTags: "",
        userId: 0
      },
      editMode: false,
      errorMessage: "",
      successMessage: "",
    });

    watch(props, async function() {
      if (props.showModal) {
        state.errorMessage = '';

        if (props.issueId !== 0) {
          state.issue = await getIssueById(props.issueId);

          console.log(state.issue)


          state.editMode = false;

        } else {
          clearIssue();
          state.editMode = true;
        }
      }
    });

    const { 
      getIssueById, 
      postIssue 
    } = issueRepository();

    function clearIssue() {
      state.issue.issueName = '';
      state.issue.issueNumber = 0;
      state.issue.publisherId = 0;
      state.issue.coverPrice = 0.00;
      state.issue.characterTags = '';
      state.issue.userId = 0;
    }

    async function validate() {
      state.errorMessage = "";
      state.successMessage = "";

      if (state.issue.issueName === "") {
        state.errorMessage = "Please enter an issue name";

      } else if (state.issue.issueNumber === "") {
        state.errorMessage = "Please enter an issue number";

      } else if (state.issue.coverPrice === "") {
        state.errorMessage = "Please enter the cover price";

      } else if (state.issue.characterTags === "") {
        state.errorMessage = "Please enter a character tag";
      }
    }

    async function submit() {
      console.log(state.issue);
      await validate();

      if (state.errorMessage === "") {
        let newId = await postIssue(state.issue);
        console.log(state.issue);
        console.log(newId);
        if (newId === 0) {
          state.errorMessage = "This issue already exists in the system";

        } else if (newId === -1) {
          state.errorMessage =
            "An error occured trying to add the issue to the system";

        } else {
          state.issue.issueName = "";
          console.log(state.issue.issueName);

          state.successMessage = "The issue has been added to the system";
        }
      }
    }

    function edit() {
      state.editMode = true;
    }

    function selectionChanged(publisherId) {
      state.issue.publisherId = publisherId;
    }

    function closeMe() {
      context.emit('close-issue', null)
    }

    return {
      state,
      postIssue,
      validate,
      submit,
      selectionChanged,
      clearIssue,
      closeMe,
      edit
    }
  }
}
</script>

