<template>
    <div>
        <!-- filtering -->
        <div class="w3-row">
            <Filtering @filtered-list="filterUpdated"/>
        </div>

        <!-- inventory dispaly -->
        <div class="w3-row">
            <div class="w3-threequarter">
                <div class="w3-col" v-if="state.userComics.length !== 0">
                    <div class="w3-quarter" v-for="(comic, comicId) in state.userComics" :key="comicId" :value="comic.issueId">
                        <div class="w3-col comic"  style="padding: 50px; cursor:pointer;">

                            <a @click="openUpdateModal(comic.issueId)"><img src="@/assets/images/comicBookCoverTemplate.jpg" style=" width: 150px;"></a>

                            <p><em>{{ comic.issueName }}</em></p>

                            <p>Issue {{ comic.issueNumber }}</p>
                        </div>
                    </div>
                </div>

                


                <!-- The collection is blank -->
                <div class="w3-col" v-if="state.userComics.length === 0">
                    <h1 class="w3-padding">Your collection is blank, please click the add button at the bottom right of the screen to add a comic</h1>
                </div>

                
            </div>

            <!-- friends list -->
            <div class="w3-quarter">
                <div class="w3-container w3-margin">
                    <div class="w3-col">
                        <div class="w3-container w3-border w3-margin-bottom">
                            <h1>Friends</h1>

                            <div v-if="state.action.length !== 0" class="friendsActivity w3-cell w3-rest w3-container w3-col w3-border w3-margin-bottom" style="overflow: scroll; height: 10%; display: block;">
                                <table class="w3-table w3-striped w3-bordered w3-centered w3-rest" style="position: sticky; display: table;">

                                    <tr class="tableHover" v-for="(activity, key) in state.action" :key="key" :id="activity">
                                        <td @click="openActivityModal" style="cursor:pointer;">{{ activity }}</td>
                                    </tr>

                                </table>
                            </div>

                            <div v-if="state.action.length === 0" class=" w3-cell w3-rest w3-container w3-col w3-border w3-margin-bottom" style="overflow: scroll; height: 10%; display: block;">
                                <table class="w3-table w3-striped w3-bordered w3-centered w3-rest" style="position: sticky; display: table;">

                                    <tr>
                                        <td><h2>Please add friends for their activity to show up here</h2></td>
                                    </tr>

                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
       <!-- Add a comic button -->
       <a @click="openAddModal" class="w3-button w3-large w3-circle w3-ripple w3-teal addButton w3-margin">+</a> 

       <Issue :showModal="state.showIssueModal" :issueId="state.selectedIssueId" @close-issue="closeIssue"/>
    </div>
</template>



<script>
    import { reactive, onMounted} from 'vue';
    //import { useRouter } from 'vue-router';
    import { comicRepository } from "@/store/repository/comicRepository.js";
    import { friendActivityRepository } from "@/store/repository/friendActivityRepository.js";
    import { appGlobal } from "@/store/global/appGlobal.js";

    import Filtering from '@/components/Filtering.vue';
    import Issue from '@/views/Issue.vue'

    export default {
        name: 'HomeScreen',

        components: {
            Filtering,
            Issue
        },

        setup() {
            const state = reactive({
                userComics: [],
                showIssueModal: false,
                selectedIssueId: 0,
                action: ["Test","Test","Test","Test","Test","Test","Test","Test","Test","Test","Test","Test","Test","Test","Test","Test","Test","Test"],
            });

            const {
                getFriends,
                getFriendActivity,
                getFriendActivityUnshown
            } = friendActivityRepository();

            const comicRepo = comicRepository();
            //const router = useRouter();
    
            onMounted(() => {
                getCollection(appGlobal.user.userId);

                appGlobal.user.friendsList = getFriends(appGlobal.user.userId);
                console.log(appGlobal.user.friendsList)
            });

            async function getCollection(ID) {
                state.userComics = await comicRepo.getAllUserComics(ID);
            }

            function openUpdateModal(issueId) {
                state.selectedIssueId = issueId;
                state.showIssueModal = true;
                
            }

            //Opens the add modal to add a comic 
            function openAddModal() {
                state.selectedIssueId = 0;
                state.showIssueModal = true;
            }

            //Opens the modal for a clicked activity item in the friends activity list
            function openActivityModal() {
                alert("The activity modal works! :)");
            }

            function closeIssue() {
                state.showIssueModal = false;
            }

            function filterUpdated(newList) {
                
                state.userComics = newList;
            }

            return {
                state,
                openAddModal,
                openUpdateModal,
                openActivityModal,
                closeIssue,
                filterUpdated,
                getFriends,
                getFriendActivity,
                getFriendActivityUnshown
            }
        }

        
    }
</script>


<style scoped>

    .addButton {
        z-index: 100;
        float: right;
        position: fixed; 
        bottom: 20px;
        right: 30px;
    }

    .comic:hover {
        background: lightgrey;
        color: black;
    }

    .friendsActivity{
        max-height: 40rem;
        overflow:hidden;
        overflow-x: scroll;
    }

    .tableHover:hover {
        background: lightgrey;
        color: black;
    }

</style>